import React, {useState} from "react";
import {Col, Container, Row} from "react-bootstrap";
import HeaderComp from "../headerComp/HeaderComp";
import {useDispatch, useSelector} from "react-redux";
import Logo from "../../assets/imgs/logo.png";
import TypewriterComponent from "typewriter-effect";
import Bg from "../../assets/imgs/banner/bg.png";
import {fetchData} from "../../redux/data/dataActions";
import {connect} from "../../redux/blockchain/blockchainActions";
import MintBox from "../mintBox/MintBox";
import ToastNoti from "../toastNoti/ToastNoti";

import "./BannerCompV2.scss";

const BannerCompV2 = () => {
  const [showShowMintButton, setShowMintButton] = useState(false);
  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  const getData = () => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {
      dispatch(fetchData(blockchain.account));
    }
  };
  const connectWalletBtnClick = (e) => {
    e.preventDefault();
    dispatch(connect());
    getData();
  };
  return (
    <Container fluid className="banner-comp-v2">
      <HeaderComp connectWallet={connectWalletBtnClick}/>
      <Row fluid className="banner-comp-v2-container">
        <Col xxl={7} xl={7} lg={12} md={12} sm={12} className="image-container">
          <img src={Bg} alt="natoverse"/>
        </Col>
        <Col xxl={5}
             xl={5}
             lg={12}
             md={12}
             sm={12}
             className="content-container d-flex flex-column justify-content-center align-content-center">
          <div className="logo-container">
            <img src={Logo} alt="natoverse"/>
          </div>
          {/* {
            !blockchain.account ? (
              <> */}
                <div className="greeting-container">
                  <h6 className="text">we are recruiting</h6>
                </div>
                <div className="title-container">
                  <TypewriterComponent
                    onInit={(typewriter) => {
                      typewriter.typeString("BE PART OF THE FIRST ARMY").start().callFunction(() => {
                        setShowMintButton(true);
                      });
                    }}
                  />
                </div>
                <div className={`button-container ${showShowMintButton && "button-container-active"}`}>
                  <button>mint soon</button>
                </div>
                {/* <div className={`description ${showShowMintButton && "button-container-active"}`}>
                  <p>Coming soon when initial Minting is done</p>
                </div> */}
              {/* </>
            ) : (
              <MintBox/>
            )
          } */}
        </Col>
      </Row>
      <ToastNoti errorMsg={blockchain.errorMsg}/>
    </Container>
  );
};

export default BannerCompV2;
